<template>
<div class="bg" id="app" @click="launchFullscreen">
  <div class="load" v-if="loading"></div>
  <div v-for="(person,index) in people" :key="person.src"  >
    <img 
    v-lazy="($env==='development' ? '': '/card') + '/static/people/' + person.src" 
    :alt="index" 
    :title="person.src"
    @click="showCard(index)"
     class="person" :style="{ transform: transform[index]}">
  </div>
  <div>
    <transition name="fade">
      <div class="card" v-show="show" >
        <img  @click="showDesc=true" :src="cardSrc" :alt="cardSrc" width="100%" style="z-index:-1" >
        <div class="icon" v-if="iconColor==='w'">
          <img src="./assets/icon/icon-w-2.svg" alt="音量" srcset="" class="icon" @click="play" v-if="playing"> 
          <img src="./assets/icon/icon-w-1.svg" alt="音量" srcset="" class="icon" @click="play" v-else> 
        </div>
        <div class="icon" v-else>
          <img src="./assets/icon/icon-b-2.svg" alt="音量" srcset="" class="icon" @click="play" v-if="playing"> 
          <img src="./assets/icon/icon-b-1.svg" alt="音量" srcset="" class="icon" @click="play" v-else> 
        </div>


      </div>
    </transition>
    <transition name="down" >
      <img @click="show=false;showDesc=false" v-show="showDesc" src="./assets/3-最深层页面.jpg" alt="" class="card" srcset="" width="100%">
    </transition>
  </div>
  <video :src="audioSrc" autoplay style="display:none" ref="audio">
      您的浏览器不支持 audio 标签。
  </video>
</div>
</template>

<script>
console.log(process.env)
export default {
  name: 'App',
  data(){
    return {
      cardSrc: '',
      loading: true,
      playing: false,
      audioSrc: '',
      activeTag: '',
      showDesc: false,
      show: false,
      people: [
        {
          src: '孔雀男-01.png',
          top: '30%',
          left: '',
          transform: ''
        },
        {
          src: '高富帅-02.png',
          top: '25%',
          left: '280'
        },
        {
          src: '钻石王老五-03.png',
          top: '',
          left: ''
        },
        {
          src: '食草男-04.png',
          top: '',
          left: ''
        },
        {
          src: '小鲜肉-05.png',
          top: '',
          left: ''
        },
        {
          src: '妈宝男-06.png',
          top: '',
          left: ''
        },
        {
          src: '经济适用男-07.png',
          top: '',
          left: ''
        },
        {
          src: '食肉女-01.png',
          top: '',
          left: ''
        },
        {
          src: '白富美-02.png',
          top: '',
          left: ''
        },
        {
          src: '白骨精-03.png',
          top: '',
          left: ''
        },
        {
          src: '猪猪女孩-04.png',
          top: '',
          left: ''
        },
        {
          src: '食肉男-05.png',
          top: '',
          left: ''
        },
        {
          src: '虎妈-06.png',
          top: '',
          left: ''
        },
        {
          src: '恨嫁女-06.png',
          top: '',
          left: ''
        },
      ]
    }
  },
  mounted(){
    setTimeout(()=>{
      this.loading = false;
    }, 2000);
  },
  methods: {
    showCard(index){
      this.show = true;
      this.activeTag = this.people[index].src.split('-')[0];
      // this.people[index].transform = 'scale(2)'
      this.cardSrc = (this.$env==='development' ? '': '/card') +'/static/2-人物展示/'+this.people[index].src.replace('png', 'jpg');
    },
    launchFullscreen(e) {
      let element = document.documentElement;
      if(element.requestFullscreen) {
        element.requestFullscreen();
      } else if(element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if(element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if(element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    },
    play(){
      // let audio = new Audio();
      // audio.src = '/static/audio/猪猪女孩.m4a';
      // audio.play();
      console.log('play');
      let activeTag = this.activeTag;
      this.audioSrc = (this.$env==='development' ? '': '/card') + `/static/audio/${activeTag}.m4a`;
      let audio = this.$refs.audio;
      audio.addEventListener('play',() => {
        this.playing = true
      })
      audio.addEventListener('ended',() => {
        this.playing = false
      })
      this.$refs.audio.play();
    }
  },
  computed:{
    iconColor(){
      if(['虎妈', '猪猪女孩', '小鲜肉'].indexOf(this.activeTag) !== -1){
        return 'b'
      }
      return 'w'
    },
    transform(){
      return this.people.map((person,index) => `translateX(${person.left || index*250}px) translateY(${person.top ||'200px'}) ${person.transform || ''}`)
    }
  }
}
</script>

<style>
*{
  padding: 0;
  margin: 0;
}
.load{
  background-image: url('./assets/loading.jpg');
  background-size: cover;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.icon{
  position: absolute;
  top: 90%;
  left: 50%;
  z-index: 99;
  transform: translateX(-50%);
  width: 60px;
}
.card{
  position: fixed;
  width: 100%;

}
html,body{
  height: 100%;
  overflow-y: hidden;
}
.bg{
  overflow-y: hidden;
  position: relative;
  height: 100vh;
  width: calc(100vh/362*2000);
  background-image: url('./assets/bg.svg');
  background-color: #3e0a9d;
  background-size: cover;
}
.person{
  width:300px;
  transition: .3s linear all;
  position:absolute;
}
.fade-enter-active{
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.down-enter-active{
  transition: transform .5s;
}
.down-enter, .down-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateY(-100vh);
}
</style>



